.playing-bar {
  color: white;
  padding: 20px;
  margin-top: -5px;
  background: black;
  width: calc(100% - 40px);
  display: grid;
  grid-template-columns: 50% 50%;
}

.playing-bar button {
  float: right;
  height: 100%;
  border: none;
  color: var(--yellow);
  font-size: 20px;
  background: var(--blue);
  cursor: pointer;
  border: 1px solid var(--yellow);
  outline: none;
}

.playing-bar button[disabled], .playing-bar button[disabled]:hover {
  opacity: 0.5;
  cursor: not-allowed;
  background: grey;
  color: lightgrey;
  border: lightgrey;
}

.playing-bar button:hover {
  background: var(--yellow);
  color: var(--blue);
}

.labels-container label{
  display: block;
}

.you {
  color: var(--yellow);
  font-size: 18px;
  font-weight: bold;
}

.computer {
  color: var(--red);
  font-size: 18px;
  font-weight: bold;
}
